import {Injectable} from '@angular/core';
import {Router} from "@angular/router";
import {ApiService} from "../@shared/services/api.service";
import { Role, RoleLists } from "./role";
import {Meta} from "../@shared/interfaces/meta";
import {AlertService} from "../@shared/services/alert.service";
import {ToastService} from "../@shared/services/toast.service";
import {NavigationService} from '../@shared/services/navigation.service'
import {BehaviorSubject} from "rxjs";

declare var $: any;

@Injectable({
    providedIn: 'root'
})
export class RolesService {

    public rolesUrl = 'roles';
    public rolesFormValid = new BehaviorSubject<boolean>(false);

    public roles: Role[] = [];
    public role: Role = <Role>{};
    public roleLists: RoleLists = {};

    public searchValue = '';
    public perPage = 10;

    public meta: Meta = null;

    public page = 1;

    public pagesContent = document.getElementById('pages-content');

    constructor(
        public api: ApiService,
        public alert: AlertService,
        public toast: ToastService,
        public router: Router,
        private navigation: NavigationService,
    ) {
    }

    public getRoles(page: any = this.page, perPage: any = this.perPage) {

        this.api.get(this.rolesUrl + '?page=' + page + '&perPage=' + perPage + '&q[name:cont]=' + this.searchValue)
            .subscribe(
                (res: any) => {
                    // @ts-ignore
                    document.getElementById('pages-content').scrollToTop(300);
                    this.roles = res.data;
                    this.roleLists = res.lists;
                    this.meta = res.meta;
                },
                (err: any) => {
                    console.error(err);
                }
            );
    }

    public editRole(id: any) {
        this.api.get(this.rolesUrl + '/' + id + '/edit')
            .subscribe(
                (res: any) => {
                    this.role = res.data.model;
                    this.roleLists = res.lists;
                },
                (err: any) => {
                    console.error(err);
                }
            );
    }

    public updateRole() {
        this.api.put(this.rolesUrl + '/' + this.role.id, {
            model: this.role,
            pivots: {
                user: this.role.users,
                permission: this.role.permissions,
            }
        }).subscribe(
            (res: any) => {
                this.toast.present(res.message, 'toast-success', 'top');
                this.navigation.back();
                this.getRoles(1);
            }
        );
    }

    public createRole() {
        this.api.get(this.rolesUrl + '/create')
            .subscribe(
                (res: any) => {
                    this.role = <Role>{};
                    this.roleLists = res.lists
                }
            );
    }

    public storeRole() {
        this.api.post(this.rolesUrl, {
            model: this.role,
            pivots: {
                user: this.role.users,
                permission: this.role.permissions,
            }
        }).subscribe(
            (res: any) => {
                this.toast.present(res.message, 'toast-success', 'top');
                this.navigation.back();
                this.getRoles(1);
            }
        );
    }

    public async deleteRole(id: any) {
        const confirm = await this.alert.confirmation(
            'Desea eliminar el item?'
        );
        if (confirm) {
            this.api.delete(this.rolesUrl + '/' + id, {})
                .subscribe(
                    (res: any) => {
                        this.toast.present(res.message, 'toast-success', 'top');
                        this.getRoles(1);
                    }
                );
        } else {
            return;
        }
    }

}
