import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';
import {RolesService} from "../roles.service";
import {ActivatedRoute} from "@angular/router";
import {Role} from "../role";
import {RolesFormComponent} from "../roles-form/roles-form.component";
import { Platform } from '@ionic/angular';

@Component({
    selector: 'app-roles-create',
    templateUrl: './roles-create.component.html',
    styleUrls: ['./roles-create.component.scss']
})
export class RolesCreateComponent implements OnInit, AfterViewInit {

    @ViewChild('roleForm') roleForm: RolesFormComponent;

    id: any = null;
    public formValid = false;

    constructor(
        public rolesService: RolesService,
        public route: ActivatedRoute,
        public platform: Platform
    ) {

    }

    ngOnInit(): void {
        this.clearPosts();
    }

    ngAfterViewInit() {
        this.roleForm.rolesForm.valueChanges.subscribe((data) => {
            this.rolesService.rolesFormValid.next(this.roleForm.rolesForm.valid);
        });
    }

    clearPosts() {
        console.log('clear');
        this.rolesService.createRole();
        this.rolesService.role = <Role>{};
    }

}
