import {Component, OnInit, ViewChild, AfterViewInit} from '@angular/core';
import {RolesService} from "../roles.service";
import {ActivatedRoute} from "@angular/router";
import {Role} from "../role";
import {RolesFormComponent} from "../roles-form/roles-form.component";
import { Platform } from '@ionic/angular';

@Component({
  selector: 'app-roles-edit',
  templateUrl: './roles-edit.component.html',
  styleUrls: ['./roles-edit.component.scss']
})
export class RolesEditComponent implements OnInit, AfterViewInit {

    @ViewChild('roleForm') roleForm: RolesFormComponent;

    public id: any = null;
    public model: Role = null;

  constructor(
      public rolesService: RolesService,
      public route: ActivatedRoute,
      public platform: Platform
  ) { }

  ngOnInit(): void {
      this.id = this.route.snapshot.paramMap.get('id');
      this.rolesService.editRole(this.id);
  }

  ngAfterViewInit() {
      this.roleForm.rolesForm.valueChanges.subscribe((data) => {
          this.rolesService.rolesFormValid.next(this.roleForm.rolesForm.valid);
      });
  }

}
